.area {
  display: none;

  &--active {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    display: block;
    width: 100%;
    max-width: 800px;
    padding: 30px;

    background-color: rgba(30,30,30,0.9);

  }

  &__counter {
    width: 40px;
    height: 40px;
    border-radius: 5px;

    background-color: #f27733;
    color: white;

    font-family: "VT323", monospace;
    text-align: center;
    line-height: 40px;
    font-size: 20px;

    &-wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 15px;
    }
  }

  &__title {
    color: white;
    letter-spacing: 3px;
  }

  &__content-wrapper {
    width: 80%;
    margin: 0 auto;
  }

  &__input-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 20px;
    margin-bottom: 10px;
  }

  &__input {
    width: 80px;
    height: 40px;
    border-radius: 5px;

    color: black;

    font-family: "VT323", monospace;
    font-size: 30px;
  }

  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 40px;
    border: 1px solid black;
    border-radius: 5px;

    background-color: #f27733;

    color: white;

    font-size: 30px;

    cursor: pointer;

    &:hover {
      transform: scale(1.05);
    }

    &:active {
      border-width: 2px;
    }
  }

  &__table-wrapper {
    max-height: 50vh;
    overflow: auto;
  }

  &__table {
    width: 100%;

    color: white;
    letter-spacing: 2px;

    text-align: center;

    & td {
      border: 1px solid silver;
      padding: 3px;
    }

    & tr:nth-child(2) td {
      font-size: 20px;
      padding: 5px;
      border-width: 3px;
      border-color: white;
    }

  }

  &__btn-end {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 150px;
    height: 40px;
    border: 1px solid black;
    border-radius: 5px;
    margin: 20px auto -15px;

    background-color: #f14b20;

    color: white;

    font-size: 30px;

    cursor: pointer;

    &:hover {
      transform: scale(1.05);
    }

    &:active {
      border-width: 2px;
    }
  }

}
