.win {
  display: none;

  &--active {
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    display: block;
    width: 100%;
    max-width: 800px;
    padding: 20px;

    background-color: rgba(30, 30, 30, 0.9);
  }

  &__title {
    color: white;

    font-size: 70px;
    text-align: center;
    font-weight: 400;
  }

  &__points-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
  }

  &__points-label {
    color: white;
  }

  &__points {

    width: 50px;
    height: 50px;

    background-color: transparent;
    border-color: transparent;

    font-family: inherit;
    font-size: 40px;

    color: white;
  }

  &__btn-close {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 150px;
    height: 40px;
    border: 1px solid black;
    border-radius: 5px;
    margin: 0 auto;

    background-color: #f17b20;

    color: white;

    font-size: 30px;

    cursor: pointer;

    &:hover {
      transform: scale(1.05);
    }

    &:active {
      border-width: 2px;
    }

  }

}
