.game {
  width: 100vw;
  height: 100vh;

  background-image: url("../images/back.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  &__title {
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    transform: translateY(-50%);

    color: #f27733;
    text-shadow: 5px 5px 5px silver;

    text-align: center;
    font-weight: 400;
    font-size: 100px;
  }

  &__btn {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 20%;

    display: flex;
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 80px;
    margin: 0 auto;
    border-radius: 10px;
    border: 1px solid black;
    box-shadow: 10px 5px 50px silver;

    background-color: #f27733;

    color: white;

    font-size: 80px;

    cursor: pointer;

    &:hover {
      transform: scale(1.05);
    }

    &:active {
      border-width: 2px;
    }
  }

  &__back {

    &--active {
      position: absolute;
      left: 0;
      right: 0;
      z-index: 1;

      height: 100vh;

      background-color: silver;
      opacity: 0.4;
    }
  }

}
